				</svg>
			</div>  
			<div v-if="!meal" class="popup__content">
				<p class="popup__text">Мы отправили СМС с кодом подтверждения на Ваш номер телефона.</p>
				<p class="popup__text">Пожалуйста ввердите данный код ниже.</p>
				<input type="text" class="form-control">

				<span class="popup__time">Код действителен: <strong>00:{{time}}</strong></span>

				<router-link to="menu"><button class="btn">Подтвердить</button></router-link>
			</div>
			<div v-else class="popup__content--meal">
				<div class="card__info--meal">
					<div class="row">
						<h2 class="card__title">{{meal.title}}</h2>
						<p class="card__price">{{meal?.priceProm || meal.price}} ₽</p>
					</div>

					<span class="card__desc">{{meal.description}}</span>
					<div class="row">
						<p class="card__compound">Состав: <br>{{meal.compound}}</p>
						<span class="card__weight">{{meal.weight}}г.</span>
					</div>
				</div>

				<div class="btn-box">
              <button @click="mealCount--" class="btn btn--card">-</button>
              <span class="meal-count">{{mealCount}} шт.</span>
              <button @click="mealCount++" class="btn btn--card">+</button>
            </div>

            <button class="btn">добавить в корзину</button>
            <button class="btn btn--white popup__close-btn" @click="closePopup">закрыть</button>
			</div>
		</div>
	</div>
	<!-- ./popup -->
</template>

<script>
export default {

	name: 'Popup',

	data() {
		return {
			mealCount: 0,
			time: 59,
			timeInterval: null
		}
	},

	props: {
		show: {
			require: true,
			type: Boolean
		},
		meal: {
			default: false,
		}
	},

	methods: {
    closePopup() {
       this.$emit("closePopup", false)
    }
  },

	watch: {
	  	show() {
	  		clearInterval(this.timeInterval)
	  		this.timeInterval = setInterval(() => this.time--, 1000)

			if(this.show && this.meal){
     			document.body.style.overflow = 'hidden'
		   } else {
   			document.body.style.overflow = 'auto'
		   }
		},

		time() {
			if (this.time == 0 && this.show && this.show) {
				clearInterval(this.timeInterval)
			}
		}
	}
}
</script>

