import axios from 'axios'

export default {
	state: {
		cart: [],
		cartQuantity: 0
	},
	mutations: {
		setCart(state, payload) {
			state.cart = payload
		},
		updateCartQuantity(state, todo) {
			if (todo == 'add') {
				state.cartQuantity++
			} else {
				state.cartQuantity--
			}
		}
	},
	getters: {
		getAllCart(state) {
			return state.cart
		},
		getCartQuantity(state) {
			return state.cartQuantity
		}
	},
	actions: {
		fetchMeals(context) {
			setInterval( context => {
				axios('http://cdn1.connect.brainlab.pro/', {})
					.then(res => context.commit("setMeals", res.data))
					limit++
			}, 900000)
		},
	},
}
