  },

  data () {
    return {
      popupShow: false,
      cartCount: 0,
      status: '',
      meal: {},

      meals: [],
    }
  },

  methods: {
    openPopup(id) {
      this.meal = this.meals[id - 1]
      this.popupShow = true
    },
    closePopup(popupShow) {
      this.popupShow = popupShow
    },
  }
}
