          />
        </div>
      </div>

      <div v-else class="menu__card">
        <h3 class="tag-title" id="breakfast">Завтраки</h3>

        <div class="card-box">
          <card
            v-for="meal in meals" :key="meal.id"
            :meal="meal"
            @openPopup="openPopup2"
          />
        </div>

        <h3 class="tag-title" id="mainMeals">основные блюда</h3>

        <div class="card-box">
         <card
            v-for="meal in mainMeals" :key="meal.id"
            :meal="meal"
            @openPopup="openPopup"
            
          />
        </div>
      </div>

      <div class="total-price">
        <div class="total-price__text">
          <p>Общая стоимость:</p> <span>2 800 ₽</span>
        </div>
