      </div>

    </div>
  </div>
  <!-- ./menu -->

  <popup :show="popupShow" :meal="meal" @closePopup="closePopup"/>

</template>

<script>
import Popup from "@/components/Popup"
import Card from "@/components/Card"

export default {

  name: 'Menu',

  components: {
    Popup,
    Card
  },

  inject: {
     store: '$store'
  },

  created() {
    // this.store.dispatch('fetchMeals')
    this.meals = this.store.getters.getAllMeals
  },

  mounted() {
    const swipeMenuItems = document.querySelectorAll('.swipe-menu__item')

    for( let swipeMenuItem of swipeMenuItems ) {
      swipeMenuItem.addEventListener("click", ()=> {

        for( let otherSwipeMenuItem of swipeMenuItems ) {
          otherSwipeMenuItem.classList.remove('swipe-menu__item--active')
        }
        swipeMenuItem.classList.add('swipe-menu__item--active')
      })
    }
  },

  data () {
    return {
      typeShow: false,
      typeText: "Меню",
      popupShow: false,

      meal: {},

      meals:[],

      mainMeals: [
        {
          id: 1,
          title: "Блинчики с джемом",
          description: "Три блинчика с нежным абрикосовым джемом и сметаной.",
          compound: "блинчики, абрикосовый джем, сметана",
          price: 800,
          weight: 350,
        },
        {
          id: 2,
          title: "Блинчики с перцем",
          description: "Три блинчика с нежным абрикосовым джемом и сметаной.",
          compound: "блинчики, абрикосовый джем, сметана",
          price: 800,
          weight: 350,
        },
      ]
    }
  },

  methods: {
    openPopup(id) {
      this.meal = this.meals[id - 1]
      this.popupShow = true
    },
    closePopup(popupShow) {
      this.popupShow = popupShow
    },

    selType(event) {
      const currentItem = event.currentTarget
      for( let menuTypeItem of document.querySelectorAll(".menu-type__items") ) {
        menuTypeItem.classList.remove("menu-type__item--active")
      }
      
      this.typeText = currentItem.innerText
      currentItem.classList.add("menu-type__item--active")

      this.typeShow = false
    }
  },
}
</script>

